import React, { useEffect } from 'react'
import { useQuery } from 'react-query';
import { useRouteMatch } from 'react-router-dom';
import { client } from '../../App';

function Users() {

  const url = useRouteMatch();
  const { id } = url.params;

  let anotherUser = useQuery("anotheruser", () => fetch("/api/users/" + id).then(res => res.json()), { cacheTime: 0 });

  return (
    <main className="profile user">
      {anotherUser.isLoading ?
        <div className="loading">It is loading...</div>
        :
        anotherUser.isError ?
          <div className="error">There was an error fetching. Please log out and try again</div>
          :
          <>

            <div className="avatar-section">
              <img className="image-picker" src={"/" + anotherUser.data.data.profile_image} alt="Profile" />
            </div>
            <div className="content">
              <h3 className="name">{anotherUser.data.data.username}</h3>
              <p>{anotherUser.data.data.profile_info}</p>
            </div>
          </>
      }
    </main>
  )
}

export default Users
